html {
  overflow-x: hidden;
}
body {
  overflow-x: hidden;
  width: 100%;
}

* {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.n-i {
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  -webkit-user-drag: none;
}

#the-parent {
  display: grid;
  grid-template-rows: auto 1fr auto;
  min-height: 100vh;
}

header {
  width: 100%;
  box-sizing: border-box;
  background-color: #393e46;
  padding: 15px 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

header h1 {
  color: #929AAB;
  font-size: 3rem;
}

header input {
  padding: 10px 7px;
  width: 200px;
  border-radius: 100px;
  outline: none;
  border: #929AAB 2px solid;
  background-color: rgba(255, 255, 255, 0.75);
  color: black;
}
header input::placeholder {
  color: gray;
  font-size: 1em;

}

footer {
  width: 100%;
  box-sizing: border-box;
  background-color: #393e46;
  padding: 15px 10px;
  color: #ebebeb;
  display: flex;
  flex-direction: column;
  gap: 20px;
  text-align: center;
}
footer ul {
  list-style: none;
}
footer a {
  color: #ebebeb;
  font-style: italic;
}

main {
  padding: 15px 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* - STYLING - */
.logo:hover {
  cursor: pointer;
}

.trinn-tema {
  width: 470px;
}

.trinn {
  margin-top: 15px;
  padding: 5px 7px 10px;
  display: flex;
  align-items: baseline;
  justify-content: space-between;
  border-bottom: #393e46 solid 2px;
}
.trinn:hover {
  cursor: pointer;
  outline: 1px rgba(128, 128, 128, 0.3) dotted;
}
.trinn:active {
  cursor: unset;
  outline: 1px rgba(128, 128, 128, 0.3) dotted;
  background-color: rgba(128, 128, 128, 0.1);
}

.trinn img {
  aspect-ratio: 1;
  height: 1.2em;
}

.undersikt {
  border-bottom: #393e46 dotted 1px;
  padding-bottom: 4px;
}

.separering-av-tabell {
  margin-top: 5px;
  display: grid;
  grid-template-columns: 2fr 1fr 1fr 1fr;
  gap: 15px;
}

.oversikt {
  justify-items: center;
}

.undersikt-sub {
  font-family: 'Times New Roman', Times, serif;
  font-size: 1.1em;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.not-available {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  justify-content: center;
  gap: 5px;
  font-size: 0.8em;
}
.not-available img {
  aspect-ratio: 1;
  height: 1em;
}

.openTabIcon {
  display: flex;
  justify-content: center;
  align-items: center;
}

.openTabIcon:hover {
  cursor: pointer;
}

.openTabIcon img {
  aspect-ratio: 1;
  height: 1.2em;
}

/* - MEDIA QUERIES - */
@media (max-width: 676px) {
  .trinn-tema {
    width: 400px;
  }
  .not-available {
    font-size: 0.6em;
  }
  header {
    flex-direction: column;
    justify-content: unset;
    gap: 15px;
  }
}
@media (max-width: 500px) {
  .trinn-tema {
    width: 100%;
  }
  .media-change-parent {
    width: 95%;
  }
  .not-available {
    font-size: 0.6em;
  }
  header {
    flex-direction: column;
    justify-content: unset;
    gap: 15px;
  }
}